                    return '<button type="button" class="btn btn-success btn-xs" ng-controller="SearchCtrl"' +
                        ' ' +
                        'ng-click="searchValue(\'' + data + '\')">' +
                        '<i class="fa fa-list-ul" aria-hidden="true"></i>' + '</button>';

